@import url("https://fonts.googleapis.com/css2?family=Urbanist:wght@300;400;500&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Urbanist", sans-serif;
}
html {
  scroll-behavior: smooth;
}
body {
  background: linear-gradient(135deg, rgb(15, 64, 197), rgb(255, 0, 0));
}
.main_div {
  overflow: hidden;
}
button:active {
  transform: scale(0.95);
}
/* navbar ==================================>> */
.navbar {
  padding: 1rem 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  letter-spacing: 1.5px;
  background: #fff;
}
.navbar .brand a {
  font-size: 1.5rem;
  text-decoration: none;
}
.navbar .hamburger {
  display: none;
}
.navbar ul {
  display: flex;
  list-style: none;
  align-items: center;
}
.navbar ul li {
  padding: 0.5rem 1rem;
  margin: 0 0.5rem;
}

.navbar ul li a {
  text-decoration: none;
}
.navbar ul li a.activeAnchor {
  color: blue;
}

.navbar ul li a:hover {
  color: blue;
}

.navbar ul .register {
  display: flex;
}

.navbar ul .register a {
  padding: 0.5rem 1rem;
  background: #fff;
  margin: 0 0.5rem;
  border-radius: 20px;
  text-decoration: none;
  transition: all 0.3s ease;
  border: 2px solid blue;
}

.navbar ul .register a:hover {
  color: #fff;
  background: blue;
  box-shadow: 0 0.2rem 1rem rgba(0, 0, 255, 0.3);
}

/* Home ==================================>> */

.main_home {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 5rem;
  color: #fff;
  letter-spacing: 1px;
}

.leftHome {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.leftHome h1 {
  font-size: 1.5rem;
}

.leftHome h1,
.leftHome p {
  margin-bottom: 1rem;
}

.leftHome p {
  line-height: 150%;
}

.leftHome button {
  padding: 0.5rem 1rem;
  border-radius: 30px;
  outline: none;
  border: 2px solid blue;
  background: #fff;
  transition: all 0.3s ease;
  cursor: pointer;
  color: blue;
  letter-spacing: 1px;
  align-self: start;
}
.leftHome button:hover {
  background: blue;
  color: #fff;
  box-shadow: 0 0.2rem 1rem rgba(0, 0, 255, 0.3);
}
.rightHome {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rightHome img {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

/* contact ==================================>> */

.contact_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 5rem;
  margin: 2rem 0;
  letter-spacing: 1px;
  color: #fff;
}
.contact_container h1 {
  margin-bottom: 2rem;
}

.contact {
  width: 100%;
  display: flex;
  gap: 2rem;
  align-items: center;
}

.contact_img {
  flex: 1;
}

.contact_img img {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.flname,
.email_mobile {
  display: flex;
  gap: 1.5rem;
}
.flname input,
.email_mobile input {
  width: 100%;
}

.contactForm {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.contactForm input[type="text"],
.contactForm input[type="tel"] {
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  outline: none;
  letter-spacing: 1px;
}
.contactForm button {
  letter-spacing: 1px;
  padding: 0.5rem 1rem;
  align-self: center;
  cursor: pointer;
  border: none;
  outline: none;
  background: blue;
  color: #fff;
  border-radius: 20px;
  transition: 0.1s ease;
}
.contactForm button:active {
  transform: scale(0.95);
}

/* services ==================================>> */

.services_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 5rem;
  letter-spacing: 1px;
  color: #fff;
}
.services_container h1 {
  margin-bottom: 2rem;
}

.services {
  display: flex;
  gap: 3rem;
}
.service_img {
  flex: 1;
}
.service_img img {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.service_text {
  flex: 1;
  display: flex;
  list-style: none;
  flex-direction: column;
  justify-content: center;
}
.service_text p {
  margin: 1rem 0;
  line-height: 150%;
}

/* about ==================================>> */
.about_container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 5rem;
  letter-spacing: 1px;
  margin: 2rem 0;
  color: #fff;
}

.about {
  display: flex;
  margin-top: 2rem;
  gap: 2rem;
}

.aboutLeft {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.aboutLeft h2 {
  margin-bottom: 2rem;
  font-size: 1.5rem;
  text-align: center;
}

.aboutRight {
  flex: 1;
}
.aboutRight img {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

/* footer ==================================>> */
footer {
  letter-spacing: 1px;
  background: #121b29;
  padding: 2rem;
  color: #fff;
}
footer .rights {
  text-align: center;
  margin-top: 2rem;
}
footer h2 {
  text-align: center;
}

.gridFooter {
  padding: 2rem 0;
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  text-align: center;
  border-bottom: 2px solid #222e42;
}
.gridFooter p {
  line-height: 150%;
}
.gridFooter h4 {
  margin-bottom: 1rem;
}
.gridFooter i {
  margin: 0 1rem;
  color: rgb(91, 91, 236);
  font-size: 1.5rem;
}

/* loading =====================================>> */

@media (max-width: 768px) {
  .navbar {
    padding: 1.5rem;
    position: relative;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.3);
  }
  .navbar ul {
    position: absolute;
    flex-direction: column;
    top: 100%;
    left: 0;
    width: 100%;
    z-index: 2;
    height: 0;
    background: #fff;
    border-bottom: 2px solid #000;
    transition: all 0.3s ease;
    overflow: hidden;
  }

  .navbar ul li,
  .navbar ul .register a {
    transition: all 0.5s ease 0.3s;
    opacity: 0;
  }
  .navbar ul.active li,
  .navbar ul.active .register a {
    opacity: 1;
  }

  .navbar ul.active {
    overflow: visible;
    height: 16rem;
  }

  .navbar .hamburger {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    width: 2rem;
    height: 1.5rem;
    justify-content: space-between;
  }
  .navbar .hamburger span {
    width: 100%;
    height: 2px;
    background: #000;
    transition: all 0.3s ease;
  }

  .navbar .hamburger span:nth-child(1),
  .navbar .hamburger span:nth-child(3) {
    transform-origin: left;
  }

  .navbar .hamburger.active span:nth-child(1) {
    transform: rotate(45deg);
  }
  .navbar .hamburger.active span:nth-child(2) {
    width: 0;
  }
  .navbar .hamburger.active span:nth-child(3) {
    transform: rotate(-45deg);
  }

  .navbar ul .register {
    flex-direction: column;
  }
  .navbar ul .register a {
    margin: 0.5rem 0;
  }

  .main_home {
    padding: 0 1.5rem;
    flex-direction: column;
  }

  .leftHome p {
    padding: 0;
  }
  .contact_container {
    padding: 0 1.5rem;
  }

  .contact,
  .flname,
  .email_mobile {
    flex-direction: column;
  }
  .contactForm {
    width: 100%;
  }
  .services_container {
    padding: 1.5rem;
  }

  .services {
    flex-direction: column;
  }
  .about_container {
    padding: 1.5rem;
  }
  .about {
    flex-direction: column;
  }

  .gridFooter {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    grid-gap: 4rem;
  }
}
